.calendar{padding:24px}.calendar .react-calendar{width:100%;border:none;color:#333333}.calendar .react-calendar button.react-calendar__tile{padding:12px 0;border:1px solid transparent;transition:all 0.2s}.calendar .react-calendar button.react-calendar__tile--now{background-color:unset;border-color:#3f51b5}.calendar .react-calendar button.react-calendar__month-view__days__day--weekend{color:#f50057}.calendar .react-calendar button.react-calendar__tile--active{background-color:#3f51b5;color:white}.calendar .react-calendar button.react-calendar__tile.sweet{background-color:#f50057;color:white}.calendar .info{margin-top:12px}.calendar .info .row{display:flex;align-items:center;font-size:14px;color:#333333;padding:4px 0;border-bottom:1px solid #dddddd}.calendar .info .label{width:80px;margin-right:12px}.calendar .info .value{flex:1;text-align:right;color:#f50057}.calendar .bottom{position:absolute;bottom:0;left:0;width:100%;padding:24px;margin-top:48px;text-align:center}.calendar .bottom a{text-decoration:none}.calendar .bottom button{width:100%}.calendar .bottom button:not(:last-child){margin-bottom:8px}
